import React, { useState } from "react";
import HomeIcon from "@mui/icons-material/Home";
import FeaturedPlayListIcon from "@mui/icons-material/FeaturedPlayList";
import AssignmentTurnedInIcon from "@mui/icons-material/AssignmentTurnedIn";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import NotificationsIcon from "@mui/icons-material/Notifications";
import SearchIcon from "@mui/icons-material/Search";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import CloseIcon from "@mui/icons-material/Close";
import Avatar from "@mui/material/Avatar";

import { Button,Input } from "@mui/material";
import "./css/QuoraHeader.css";
import Modal from "react-responsive-modal";
import "react-responsive-modal/styles.css";

function QuoraHeader() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  
  const Close = <CloseIcon />;
  return (
    <div className="qHeader">
      <div className="qHeader-content">
        <div className="qHeader__logo">
          <img
            src="https://assets.entrepreneur.com/content/3x2/2000/20190211224126-quora-logo-crop.jpeg"
            alt="logo"
          />
        </div>

        <div className="qHeader__icons">
          <div className="qHeader__icon">
            <HomeIcon />
          </div>
          <div className="qHeader__icon">
            <FeaturedPlayListIcon />
          </div>
          <div className="qHeader__icon">
            <AssignmentTurnedInIcon />
          </div>
          <div className="qHeader__icon">
            <PeopleAltIcon />
          </div>
          <div className="qHeader__icon">
            <NotificationsIcon />
          </div>
        </div>

        <div className="qHeader__input">
          <SearchIcon />
          <input type="text" placeholder="Search Quora" />
        </div>
        <div className="qHeader__Rem">
          <Avatar />
        </div>
        <Button onClick={() => setIsModalOpen(true)}>Add Question</Button>
        <Modal
          open={isModalOpen}
          closeIcon={Close}
          onClose={() => setIsModalOpen(false)}
          closeOnEsc
          center
          closeOnOverlayClick={false}
          styles={{
            overlay: {
              height: "auto",
            },
          }}
        >
          <div className="model__title">
            <h5>Add Question</h5>
            <h5>Share Link</h5>
          </div>
          <div className="modal__info">
            <Avatar className="avatar" />
            <div className="modal__scop">
              <PeopleAltIcon />
              <p>Public</p>
              <ExpandMoreIcon />
            </div>
          </div>
          <div className="modal__Field">
            <Input type ="text" placeholder="Start your questin with 'What,'How,'Why',etc   "  />
            <div style={{
              display:"flex",
              flexDirection:"column"
              }}>
                <input 
                type="text"  
                style={{
                  margin:"5px 0",
                  border:"1px solid lightgray",
                  padding:"10px",
                  outline:"2px solid black"

                }}
                placeholder="Optional:include a link tat gives context" />

            </div>
            
          </div>
          <div className="modal__buttons">
            <button className="cancle"  onClick={()=>setIsModalOpen(false)} >
              cancel
            </button>
            <button type="submit" className="add"  >
              Add Question
            </button>
          </div>
        </Modal>
      </div>
    </div>
  );
}

export default QuoraHeader;
